p, ul, ol {
	line-height: 1.5;
}

a {
	text-decoration: none;
	color: currentColor;
}

p a { text-decoration: underline; }

input,
button,
select,
textarea {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;

	border: none;
	background: none;
}

select {
	border-radius: 0;
}

.shadow {
	-webkit-box-shadow: 0 2em 6em 0em rgba(0, 0, 0, .5);
	        box-shadow: 0 2em 6em 0em rgba(0, 0, 0, .5);
}

::-webkit-input-placeholder {
  color: rgba(0, 0, 0, .5);
}

.hidden { display: none; }

.alignleft { float: left !important; }
.alignright { float: right !important; }

.left   { text-align: left; }
.center { text-align: center; }
.right  { text-align: right; }
.middle {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.abs-center {
	position: absolute;
  left: 0; top: 0;
  right: 0; bottom: 0;
  margin: auto;
}

.wrapper {
	width: 100%;
	max-width: 67.5em;
	padding-left: 1.5em;
	padding-right: 1.5em;
	margin: auto;
}

.wrapper.small {
	max-width: 42em;
}

.row::after {
	content: '';
	display: block;
	clear: both;
}

.spacing {
	display: block;
	height: 4em;
}

.fullheight {
	height: 100vh;
}

[class*="col-"] {
	display: block;
	float: left;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	min-height: 1px;
	padding: 0 .5em;

	&.nopadding {
		padding: 0;
	}
}

.col-1  { width:   8.33333%; }
.col-2  { width:  16.66666%; }
.col-3  { width:  25%;       }
.col-4  { width:  33.33333%; }
.col-5  { width:  41.66666%; }
.col-6  { width:  50%;       }
.col-7  { width:  58.33333%; }
.col-8  { width:  66.66666%; }
.col-9  { width:  75%;       }
.col-10 { width:  83.33333%; }
.col-11 { width:  91.66666%; }
.col-12 { width: 100%;       }

@media screen and (min-width: 721px) {
