	.right-m  { text-align: right; }
	.middle-m {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
	}

	.hidden-m { display: none; }
}

@media screen and (max-width: 480px) {
	.wrapper {
		padding-left: .5em;
		padding-right: .5em;
	}

	.col-s-1  { width:   8.33333%; }
